@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 
body {
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

h1 {
  margin-bottom: 30px;
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}



.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  background: tomato;
  color: white;
  padding: 10px
}

.homeButton {
    padding: 10px 30px;
    font-weight: 600;
    font-size: 1.3em;
    border: 2.5px solid #4F6D7A;
    color:#4F6D7A;
}

.homeButton:hover {
  color: rgb(30, 42, 48); 
  text-decoration: none;
  border-color: rgb(30, 42, 48);

}

.homePhrase {   
    padding: 8% 2% 12% 12%;
    background-color: rgb(241, 233, 207);
}

h1 {
    color: black;
    font-size: 4.5em;
    font-weight: bold;
    margin-bottom: 15%;
}

.blueRow {
    padding: 10% 12% 12% 10%;
    background-color: rgb(81, 124, 143);
    color: #E8DAB2;
}

h2 {
    font-weight: 600;
    font-size: 2.5em;
}

/* .travolta {
    background-image: url('../../../../public/Travolta.png');
} */

.homeFooter {
    background-color: rgb(24, 49, 61);
    padding: .5%;
    color: #E8DAB2;
    display: flex;
    justify-content: center;
}

.homeFooter p {
    margin-top: 1%;
    text-align: center;
}
.nav {
    background-color: #DD6E36;
    color: #E8DAB2;
}

.nav a {
    color: #E8DAB2;
    font-weight: 600;
    transition: 0.3s;
}

.nav a:hover {
    color: #EAEAEA
}
