.homeButton {
    padding: 10px 30px;
    font-weight: 600;
    font-size: 1.3em;
    border: 2.5px solid #4F6D7A;
    color:#4F6D7A;
}

.homeButton:hover {
  color: rgb(30, 42, 48); 
  text-decoration: none;
  border-color: rgb(30, 42, 48);

}

.homePhrase {   
    padding: 8% 2% 12% 12%;
    background-color: rgb(241, 233, 207);
}

h1 {
    color: black;
    font-size: 4.5em;
    font-weight: bold;
    margin-bottom: 15%;
}

.blueRow {
    padding: 10% 12% 12% 10%;
    background-color: rgb(81, 124, 143);
    color: #E8DAB2;
}

h2 {
    font-weight: 600;
    font-size: 2.5em;
}

